.Container2 {
  width: 100%;
  height: 100vh;
}

.Container2 .video {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.Container2 .video .center_text {
  background: rgb(11, 189, 227);
  padding: 20px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.Container2 .video .center_line {
  border: 1px solid rgb(211, 189, 186);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 170px;
  height: 70px;
  border-radius: 20px 0 20px 0;
  cursor: pointer;
  z-index: 2;
  transition: all 0.5s ease;
}

.Container2 .video .center_line:hover {
  border-radius: 0;
}

.Container2 .video video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  object-position: center top;
}
